<template>
    <div>
         <div class="jumbotron jumbotron-fluid jumbotron-bg d-flex align-items-center">
          <div class="container">
         <div class="bg-lighter py-3 mt-5">
        <h1 class="display-4 font-weight-bold ml-3"></h1>
        <p class="lead font-weight-bold ml-4">Supreme</p>
        <a href="" @click.prevent="getHome" class="lead font-weight-bold ml-4 text-danger" ></a>
        </div>
        </div>
        </div>

    </div>
</template>

<script>
export default {
    methods:{
        getHome(){
            this.$router.push('/Home')
        }
    }
}
</script>

<style scoped>
.jumbotron-bg{
    background-image: url(https://newparadiso.tw/wp-content/uploads/2019/02/S-08907.jpg);
    background-position:center center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 590px;
    font-family: "";
}

.bg-lighter{
    background-color: rgba(255,255,255,.65); 
    /* // */
}

a:hover{
    text-decoration: none;
}
</style>



