//vueindex.html
//
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import Loading from 'vue-loading-overlay';          //Loading
import 'vue-loading-overlay/dist/vue-loading.css';  //Loading
import 'bootstrap';              //bootstrapjqueryimport
import "jquery";                 //import jquery
import VeeValidate from 'vee-validate';
//
import App from './App'  ;
import router from './router';
import './bus' ;                     //
import currencyFilter from './Filter/currency'; //
import date from './Filter/date';
import VueI18n from 'vue-i18n';
import zhTW from 'vee-validate/dist/locale/zh_TW';
import {Validator} from 'vee-validate';




        //
Vue.component('Loading',Loading)         //component
Vue.use(VueAxios, axios)                //axios
Vue.filter('currency',currencyFilter)  //filter
Vue.filter('date',date)               //
Vue.use(VeeValidate);
Vue.use(VueI18n);                      //
Vue.use({Validator})
VeeValidate.Validator.localize('zh_TW',zhTW) //Email
Vue.config.productionTip = false   //cookies
axios.defaults.withCredentials = true




const i18n = new VueI18n({
  locale: 'zhTW'
});
Vue.use(VeeValidate, {
  i18n,
  dictionary: {
    zhTW
  },
  events: 'input',
});








new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,              //
  i18n,
});


//
router.beforeEach((to,from,next)=>{
  if(to.meta.requiresAuth){
    const api =`${process.env.APIPATH}/api/user/check`;
    axios.post(api).then((response) => {
   console.log(response.data)
   if(response.data.success){
    next();
   }else{
     alert('')
    next({
      path:'/login'
    });
   }
   })
  }else{
    next();
  }
})




