
<template>
     <div>
         <loading :active.sync="isLoading" ></loading>     <!-- loading -->
    <div class="text-right mt-4">
      <button class="btn btn-dark" @click='openModal(true)'></button>      <!-- openModal(true) = isNew = true -->
    </div>
    <table class="table mt-4 table-dark table-hover ">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="80"></th>
          <th width="80"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
            {{ item.origin_price | currency}}
          </td>
          <td class="text-right">
            {{ item.price | currency}}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-light btn-sm" @click="openModal(false,item)"></button>   <!-- openModal(false,item) = isNew = false -->
          </td>
          <td>
            <button class="btn btn-danger btn-sm" @click="opendelProductModal(item)"></button>   
          </td>
        </tr>
      </tbody>
    </table>

 <!-- pagination -->
<Pagination :pagination="pagination"  @getPageProducts="getProducts"/>   <!-- paginationgetPageProductsgetProducts -->
<!-- Modal -->
<Modal :tempProduct='tempProduct'  @getupdateProducts="updateProduct" ref='refs' />
<!-- RemoveModal -->
<RemoveModal :tempProduct='tempProduct' @getRemoveDatas="removeData"/>
  </div>
</template>


<script>
/* global $ */    //ESLintimport $ from "jquery"
import $ from "jquery";
import Pagination from '../Pagination';
import RemoveModal from '../RemoveModal';
import Modal from '../Modal';

export default {
    data(){
        return{
            products:[],                         //
            pagination:{},                       //
            tempProduct:{},                    //tempProduct
            isNew:Boolean,                    //
            isLoading:false,                 //loading
        };
    },
    methods:{
        getProducts(page = 1){     //ES61
        const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/products?page=${page}`;       //API products 
        const vm =this;
        vm.isLoading =true;                              //loading
        this.$http.get(api).then((response) => {
        console.log('getProducts',response.data)
        vm.products = response.data.products;
        vm.isLoading =false;                            //loading
        vm.pagination = response.data.pagination        //pagination
})
       },
       openModal(isNew,item){
           let vm = this;
           if(isNew){                                         //isNewtrueModal
               this.tempProduct ={};                         //tempProduct
               this.$refs.refs.$refs.files.value = ''                    //inputvalue
              //  console.log(this.$refs.refs.$refs.files.value)

               this.isNew = true;
           }else{                                             //isNewfalseModal
               this.tempProduct = Object.assign({},item);     //Object.assign({},item)
               this.isNew = false;
           }
           $('#productModal').modal('show');     //vuemethodsbootstrapjquery

       },
       updateProduct(){
        let api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/product`;    //API products 
        let httpMethod = 'post';
        const vm =this;
        if(!vm.isNew){                                                                                         //isNewtrueisNewfalse
           api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/product/${vm.tempProduct.id}`;    //API
           httpMethod = 'put';                                                                                //"put"
        }      
        this.$http[httpMethod](api,{data:vm.tempProduct}).then((response) => {                    //{data:{}} [httpMethod]
        console.log('updateProduct',response.data)
        if(response.data.success){                      //
            $('#productModal').modal('hide');           //modal
            vm.getProducts();                           //
        }else{
            $('#productModal').modal('hide');
            vm.getProducts();
            alert('')
        }
})
       },
        opendelProductModal(item){
          $('#delProductModal').modal('show');                      //model
          this.tempProduct = Object.assign({},item);                //itemtempProduct Object.assign({},item)

       },
       removeData(tempProduct){                                     //modeltempProduct
          const vm =this;
          const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/product/${vm.tempProduct.id}`;     //API
          this.$http.delete(api).then((response) => {
          console.log('removeData',response.data)
          vm.getProducts();                           //
         })
         $('#delProductModal').modal('hide');         //modal     
       },
    },
    created(){
         this.getProducts()                    //created()getProducts()
    },
    components:{
        Pagination,
        RemoveModal,
        Modal,
    }
}
</script>




